<template>
	<!-- not shown in technical steps like error or maintenance -->
	<div
		v-if="step < 10"
		class="step_progress"
		data-cy="pagination"
	>

		<!-- 1 -->
		<div
			:class="step_info_class(1)"
			class="step_info"
		>
			<div class="step_title">Základné údaje</div>
			<div class="step_number">1</div>
			<div
				v-if="step === 1"
				class="step_tabs"
			>
				<span
					v-for="index in tab_count"
					:class="step_tab_class(index)"
				></span>
			</div>
		</div>

		<!-- line 1 -->
		<div
			:class="step_line_class(1)"
			class="step_line"
		></div>

		<!-- 2 -->
		<div
			:class="step_info_class(2)"
			class="step_info"
		>
			<div class="step_title">Výber poisťovne</div>
			<div class="step_number">2</div>
		</div>

		<!-- line 2 -->
		<div
			:class="step_line_class(2)"
			class="step_line"
		></div>

		<!-- 3 -->
		<div
			:class="step_info_class(3)"
			class="step_info"
		>
			<div class="step_title">Údaje o poistníkovi</div>
			<div class="step_number">3</div>
			<div
				v-if="step === 3"
				class="step_tabs"
			>
				<span
					v-for="index in tab_count"
					:class="step_tab_class(index)"
				></span>
			</div>
		</div>

		<!-- line 3 -->
		<div
			:class="step_line_class(3)"
			class="step_line"
		></div>

		<!-- 4 -->
		<div
			:class="step_info_class(4)"
			class="step_info"
		>
			<div class="step_title">Uzatvorenie zmluvy</div>
			<div class="step_number">4</div>
		</div>

		<!-- line 4 -->
		<div
			:class="step_line_class(4)"
			class="step_line"
		></div>

		<!-- 5 -->
		<div
			:class="step_info_class(5)"
			class="step_info"
		>
			<div class="step_title">Hotovo!</div>
			<div class="step_number">5</div>
		</div>

	</div>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import STATE_forms from '@/store/STATE_forms.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			step_info_class(int)
			{
				if (int < this.step) {
					return 'completed';
				}
				else if (int === this.step) {
					return 'current';
				}
				else {
					return '';
				}
			},
			step_line_class(int)
			{
				if (int < this.step) {
					return 'completed';
				}
				else if (int === this.step) {
					return 'pusher';
				}
				else {
					return '';
				}
			},
			step_tab_class(int)
			{
				if (int < STATE_forms().get_tab_page().current) {
					return 'completed';
				}
				else if (int === STATE_forms().get_tab_page().current) {
					return 'current';
				}
				else {
					return '';
				}
			},
		},
		computed: {
			step()
			{
				return STATE_core().current_step();
			},
			tab_count()
			{
				return STATE_forms().get_tab_page().last;
			},
		},
		watch: {
		},
	}
</script>
