<template>
	<Modal_body :close="true" title="Naskenovanie dokladov" >

		<!-- two sides, front and back -->
		<div
			v-for="position in 2"
			class="col-12"
		>
			<div class="input-area">
				<div class="drag-n-drop">

					<!-- camera switch button -->
					<button
						@click="camera_toggle(position)"
						class="btn btn-primary dd-camera"
					>
						<span class="i_camera"></span>
					</button>

					<!-- picture capture -->
					<Scan_capture
						v-if="active_cameras[position]"
						:position="position"
						@scan_payload="scan_payload"
						@scan_feedback="add_to_feedback"
					/>

					<!-- upload picture -->
					<Scan_upload
						v-else
						:position="position"
						:debug="debug"
						@scan_payload="scan_payload"
						@scan_feedback="add_to_feedback"
					/>

				</div>
			</div>
		</div>

		<!-- check request and fill data -->
		<Scan_check
			:debug="debug"
			:side_payloads="side_payloads"
			@scan_feedback="add_to_feedback"
		/>

		<!-- feedback -->
		<Scan_feedback :feedback="feedback" />

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Scan_capture from '@/components/modals/modal/scan_op/Scan_capture.vue';
	import Scan_check from '@/components/modals/modal/scan_op/Scan_check.vue';
	import Scan_feedback from '@/components/modals/modal/scan_op/Scan_feedback.vue';
	import Scan_upload from '@/components/modals/modal/scan_op/Scan_upload.vue';

	export default {
		props: {
		},
		components: {
			Modal_body,
			Scan_capture,
			Scan_check,
			Scan_feedback,
			Scan_upload,
		},
		emits: [],
		data() {
			return {
				feedback: [],
				debug: false,
				side_payloads: {
					1: '',
					2: '', // base64 string goes here
				},
				active_cameras: {
					1: false,
					2: false,
				},
			}
		},
		created() {
		},
		methods: {
			scan_payload(payload, position)
			{
				this.side_payloads[position] = payload;
			},
			add_to_feedback(feed)
			{
				this.feedback.push(feed);
			},
			camera_toggle(position)
			{
				this.active_cameras[position] = !this.active_cameras[position];
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
