<template>

	<!-- file dropped -->
	<div
		@dragenter.prevent
		@dragover.prevent
		@drop.prevent="file_change"
	>

		<!-- upload preview -->
		<img
			v-if="success"
			:src="file.url"
			class="scan_base64"
		>

		<!-- preview placeholder -->
		<span
			v-else
			class="i_cloud_upload"
		></span>

		<!-- dragndrop instructions -->
		<p class="dd-title">

			{{ position === 1 ? 'Predná' : 'Zadná' }} strana -

			<!-- filename -->
			<span class="dd-file">
				{{ success ? file.name : 'Presuňte súbor sem (JPEG, PNG)' }}
			</span>

			<!-- icon -->
			<span
				v-show="success"
				class="i_check"
			></span>

		</p>

		<!-- upload instructions -->
		<p>alebo</p>
		<div class="input-file-button mt-3">

			<!-- upload button -->
			<label
				class="btn btn-grey"
				:for="'scan_input_' + position"
			>
				Vybrať súbor
				<input
					:id="'scan_input_' + position"
					:name="'scan_input_' + position"
					type="file"
					accept="image/*"
					capture=""
					@change="file_change"
				>
			</label>

		</div>

	</div>

</template>

<script>
	import Credentials_requests from '@/logic/requests/Credentials_requests.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			debug: Boolean,
			position: Number, // 1 = front side, 2 = back side
		},
		components: {
		},
		emits: ['scan_feedback', 'scan_payload'],
		data() {
			return {
				file: {
					whole_file: '', // whole file object, used in converter service
					valid: false, // if file passed FE validation
					name: '', // filename to display for user
					size: '', // file size for validation
					type: '', // file type for validation
					url: '', // used for image preview
					base: '', // converted to base64
				},
				success: false, // shows filename, icon and image preview
			}
		},
		created() {
		},
		methods: {
			file_change(event)
			{
				// change and drop events has different place for file
				let file_from_event = {};
				if (event.type === 'change') {
					file_from_event = event.target?.files?.[0];
				}
				else if (event.type === 'drop') {
					file_from_event = event.dataTransfer?.files?.[0];
				}

				// fallback, file wasnt uploaded, can it even happen?
				if (file_from_event === undefined) {
					return;
				}

				// cozier file structure
				this.file = {
					whole_file: file_from_event,
					valid: false,
					name: Functions.slugify_keep_extension_add_tag(file_from_event.name, false),
					size: file_from_event.size,
					type: file_from_event.type,
					url: URL.createObjectURL(file_from_event),
					base: ''
				};

				// FE validation
				this.scan_validate();
				if (this.file.valid === false) {
					this.success = false;
					// delete previously stored base
					this.$emit('scan_payload', '', this.position);
					return;
				}

				// convert and send to backend
				this.convert_base64();
			},
			scan_validate()
			{
				// size
				if (this.file.size > 15000000 /*15mb*/) {
					this.$emit('scan_feedback', 'Súbor je príliš veľký: ' + this.file.size + ', povolená veľkosť: 15mb');
					return;
				}

				// type
				if (this.file.type !== 'image/png' && this.file.type !== 'image/jpg' && this.file.type !== 'image/jpeg') {
					this.$emit('scan_feedback', 'Nepodporovaný typ súboru: ' + this.file.type + ', povolené typy: .png .jpg .jpeg');
					return;
				}

				// report
				if (this.debug) {
					this.$emit('scan_feedback', 'file ' + this.file.name + ' valid');
				}

				// valid, will continue the flow
				this.file.valid = true;
			},
			convert_base64()
			{
				// prepre converter service
				let reader = new FileReader();

				// listener when converter service is finished
				reader.addEventListener(
					'load',
					(event) => {

						// save base64
						this.file.base = event.target.result;

						// report
						if (this.debug) {
							this.$emit('scan_feedback', 'converted file ' + this.file.name + ' to base64');
						}

						// store base64 in parent, scan check is sending them
						this.$emit('scan_payload', this.file.base, this.position);

						// success
						this.success = true;
					}
				);

				// launch converter service
				reader.readAsDataURL(this.file.whole_file);
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
