<template>
	<section class="text-center mt-5">
		<p>Kontrolujem stav ponuky.</p>
		<Bouncer_loading
			size="small"
		/>
	</section>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import Group_user_form from '@/logic/requests/Group_user_form.js';
	import Session_data from '@/logic/requests/Session_data.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';

	export default {
		props: {
		},
		components: {
			Bouncer_loading,
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
			// check session state
			Session_data.session_info().then(
				(response) => {
					if (response.status !== 200) {
						return false;
					}
					// confirmed session (state 100+) goes to step5
					if (response.result.state >= 100) {
						STATE_core().change_step(5);
						return false;
					}
					return true;
				}
			).then(
				(go) => {
					if (!go) {
						return;
					}
					// check if form is valid and go to step2, if not let him edit in step1
					Group_user_form.validate_group_form().then(
						(response) => {
							if (response.status !== 200) {
								return;
							}
							// valid to step2
							if (response.result.validform === true) {
								STATE_core().change_step(2);
							}
							// invalid step1
							else {
								STATE_core().change_step(1);
							}
						}
					);
				}
			);
		},
		methods: {
		},
		computed: {
		},
		watch: {
		},
	}
</script>
