<template>
	<section>

		<Title
			:title="title"
			:subtitle="subtitle"
		/>

		<div v-if="success !== null" class="row gx-3 gy-3">

			<!-- 1 payment -->
			<Point
				v-if="!success"
				:order="1"
				title="Uhradiť poistenie"
			>
				<div
					class="row"
				>
					<div class="col-md-4 mb-3 mb-md-0">
						<!-- payment gateways -->
						<Gateways />
					</div>
					<div class="col-sm-6 col-md-4 mb-3 mb-md-0">
						<!-- payment info -->
						<Info />
					</div>
					<div class="col-sm-6 col-md-4">
						<!-- payment qr code -->
						<Qr />
					</div>
				</div>
			</Point>

			<!-- 2 documents -->
			<Point
				:order="success ? 1 : 2"
				title="Uložiť dokumenty"
			>
				<Documents_prepare
					:start_doc_requests="start_doc_requests"
					step_html="5"
				/>
			</Point>

			<!-- 3 next steps -->
			<Point
				:order="success ? 2 : 3"
				title="E-mail a ďalšie inštrukcie"
			>
				<p>
					Do 30 dní dostanete poštou potvrdenie o uzatvorení zmluvy ako aj potrebné dokumenty (biela karta, zelená karta,...).
				</p>
				<p>
					Všetky tieto informácie a dokumenty sme Vám poslali aj e-mailom, aby ste sa k nim vedeli kedykoľvek dostať.
				</p>
				<p class="text-warning">
					<strong>
						Ak je potrebné ešte niečo doplniť pre poisťovňu, tak podrobný postup nájdete rovnako v texte e-mailu.
					</strong>
				</p>
			</Point>

		</div>

	</section>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import Documents_prepare from '@/components/form_app/documents/Documents_prepare.vue';
	import Gateways from '@/components/form_app/payment/Gateways.vue';
	import Info from '@/components/form_app/payment/Info.vue';
	import Qr from '@/components/form_app/payment/Qr.vue';
	import Session_data from '@/logic/requests/Session_data.js';
	import STATE_params from '@/store/STATE_params.js';
	import Title from '@/components/form_app/payment/html_slots/Title.vue';
	import Point from '@/components/form_app/payment/html_slots/Point.vue';
	import Payment_requests from '@/logic/requests/Payment_requests.js';

	export default {
		props: {
		},
		components: {
			Documents_prepare,
			Gateways,
			Info,
			Qr,
			Title,
			Point,
			Payment_requests,
		},
		emits: [],
		data() {
			return {
				success: null,
				start_doc_requests: false,
			}
		},
		created() {

			// getdata for selected_product which is needed for documents
			Promise.resolve().then(
				() => {
					return Session_data.get_data();
				}
			).then(
				(response) => {

					// set selected_product if session was reopened
					STATE_params().extract_selected_product(response);

					// start document requests
					this.start_doc_requests = true;

				}
			);

			// check for payment success
			let payment = STATE_core().get_payment_data();
			if (!payment.payment_id || !payment.payment_result) {
				console.log('ERROR: payment is empty', structuredClone(payment));
				return;
			}

			// dont have URL params for this request yet
			Payment_requests.payment_result(payment.payment_id, payment.payment_result).then(
				(response) => {
					console.log(response.result);
					this.success = response.result === 'OK' ? true : false;
				}
			);

		},
		methods: {
		},
		computed: {
			title()
			{
				if (this.success === null) {
					return 'Spracovávam platobnú informáciu...';
				}
				return this.success ? 'Platba úspešná!' : 'Platba neúspešná!';
			},
			subtitle()
			{
				if (this.success === null) {
					return '';
				}
				return this.success ? 'Gratulujeme! Vaša zmluva je platná.' : 'Platba nebola úspešná alebo informácia a zrealizovaní platby je nedostupná. Prosím skontrolujte si výsledok cez internetbanking.';
			},
		},
		watch: {
		},
	}
</script>
