import { defineStore } from 'pinia';
import Cookie_manager from '@/logic/Cookie_manager.js';

export default defineStore(
	'STATE_core',
	{
		state: () => {
			return {
				access_token: '', // brearer string
				get_access_token_promise: '', // promise, returns string, false, 503
				session_token: '', // uuid string
				endpoint_type: '', // minor_stage | stage | edu | production | pg_production | develop (brano) | dev (gregor)
				run_mode: '', // form_app | node_red | docs_landing
				backend_type: '', // vue | php | lukas_local | myport
				group_id: 0, // int
				step: 0, // int -1 | 0 | 0 | 1 | 2 | 3 | 4 | 5 | 6
				skin: '', // finportal | najpoistenie | partners
				payment: {
					// payment_id: '', // uuid
					// payment_result: '', // OK | FAIL | TOUT
					// payment_debug: false, // true | false | undefined
				},
				cookie_session_manager: false, // true | false | undefined
				proxy_endpoint: '', // optional (gregor only)
				proxy_scope: '', // b2b | backoffice | b2c | api (gregor only)
			};
		},
		getters: {
			// step
			current_step()
			{
				return () => {
					return this.step;
				}
			},
			// group
			product_group_id()
			{
				return () => {
					return this.group_id;
				}
			},
			// session
			get_session_token()
			{
				return () => {
					return this.session_token;
				}
			},
			// access
			give_bearer()
			{
				return () => {
					return 'Bearer ' + this.access_token;
				}
			},
			myport_access_token_promise()
			{
				return () => {
					return this.get_access_token_promise({refresh: true});
				}
			},
			// endpoint
			get_endpoint_type()
			{
				return () => {
					return this.endpoint_type;
				}
			},
			endpoint_url()
			{
				return () => {
					let type = this.get_endpoint_type();
					let endpoints = {
						minor_stage: 'https://api-minor-stage.finportal.sk/esb/online-sale-mw/',
						stage: 'https://api-stage.finportal.sk/esb/online-sale-mw/',
						edu: 'https://api-edu.finportal.sk/esb/online-sale-mw/',
						production: 'https://backoffice.finportal.sk/esb/online-sale-mw/',
						pg_production: 'https://api.partnerspoistenie.sk/esb/online-sale-mw/',
						develop: 'https://api-develop.intechon.sk/esb/online-sale-mw/',
						dev: this.proxy_endpoint + '/online-sale-mw/',
					};
					if (!endpoints.hasOwnProperty(type)) {
						console.log('baaad endpoint_type');
						return null;
					}
					return endpoints[type];
				}
			},
			oauth_url()
			{
				return () => {
					return this.endpoint_url().replace('/online-sale-mw/', '') + '/auth/oauth/v2/token/';
				}
			},
			get_backend_type()
			{
				return () => {
					return this.backend_type;
				}
			},
			// scope
			give_api_scope()
			{
				return () => {
					return this.proxy_scope;
				}
			},
			// skin
			skin_class()
			{
				return () => {
					return 'skin-' + this.skin;
				}
			},
			is_partners_skin()
			{
				return () => {
					return this.skin === 'partners';
				}
			},
			get_skin_type()
			{
				return () => {
					return this.skin;
				}
			},
			// mode
			get_run_mode()
			{
				return () => {
					return this.run_mode;
				}
			},
			// payment
			get_payment_data()
			{
				return () => {
					return this.payment;
				}
			},
			// manager
			is_cookie_manager_on()
			{
				return () => {
					return this.cookie_session_manager;
				}
			},
		},
		actions: {
			// all
			save_the_config(config)
			{
				// agent_code & filtered_products is elsewhere
				this.access_token				= config.access_token;
				this.get_access_token_promise	= config.get_access_token_promise || null;
				this.session_token				= config.session_token;
				this.endpoint_type				= config.endpoint_type;
				this.run_mode					= config.run_mode;
				this.backend_type				= config.backend_type;
				this.group_id					= config.group_id;
				this.step						= config.step;
				this.skin						= config.skin;
				this.payment					= config.payment || {};
				this.cookie_session_manager		= config.cookie_session_manager || false;
				this.proxy_endpoint				= config.proxy_endpoint;
				this.proxy_scope				= config.proxy_scope;
			},
			// step
			change_step(int)
			{
				this.step = int;
				Cookie_manager.update_step();
			},
			// group
			change_product_group_id(int)
			{
				this.group_id = int;
			},
			// session
			set_session_token(token, overwrite)
			{
				if (overwrite === false && this.session_token !== '') {
					return;
				}
				this.session_token = token;
				Cookie_manager.update_session();
			},
			// access
			save_bearer(auth)
			{
				this.access_token = auth;
				if (auth === '503' || auth === 503) {
					this.change_step(503);
				}
			},
		},
	}
);
