<template>
	<p>cookie session manager - experimentalna feature</p>
	<section class="session_cookie_manager">
		<table class="table table-striped table-hover table-borderless align-middle table-sm">
			<thead>
				<tr>
					<th scope="col">ID</th>
					<th scope="col">name</th>
					<th scope="col">step</th>
					<th scope="col">token</th>
					<th scope="col">expire</th>
					<th scope="col">open</th>
				</tr>
			</thead>
			<tbody>
				<tr
					v-for="(name, id) in product_groups"
				>
					<th scope="row">{{ id }}</th>
					<td>{{ name }}</td>
					<td>{{ find_step(id) }}</td>
					<td>{{ find_token(id) }}</td>
					<td>{{ find_expiration(id) }}</td>
					<td>
						<button
							@click="open_this_group(id)"
							class="btn btn-sm btn-primary"
						>
							open
						</button>
					</td>
				</tr>
			</tbody>
		</table>
	</section>
</template>

<script>
	import Constants from '@/logic/Constants.js';
	import Cookies from '@/logic/Cookies.js';
	import STATE_core from '@/store/STATE_core.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			find_token(id)
			{
				return Cookies.get_cookie('osm_' + id + '_session') || '';
			},
			find_step(id)
			{
				return Cookies.get_cookie('osm_' + id + '_step') || 1;
			},
			find_expiration(id)
			{
				let expiration = Cookies.get_cookie('osm_' + id + '_expire');
				if (!expiration) {
					return '';
				}
				expiration = new Date(expiration);
				expiration = expiration.toLocaleString('sk-SK');
				return expiration;
			},
			open_this_group(id)
			{
				STATE_core().set_session_token(this.find_token(id), false);
				STATE_core().change_product_group_id(id);
				STATE_core().change_step(parseInt(this.find_step(id), 10));
			},
		},
		computed: {
			product_groups()
			{
				return Constants.group_names();
			},
		},
		watch: {
		},
	}
</script>

<style lang="scss">
	.session_cookie_manager {
		button {
			padding: 4px 12px;
		}
	}
</style>
