<template>
	<section>

		<!-- Title -->
		<Step_title :loaded="loaded" :total="total" />

		<div class="results_action_area">

			<!-- Suhrn udajov -->
			<Summary :user_data="user_data" />

			<!-- globalForm -->
			<Global_form
				:global_disabled="global_disabled"
				@calculate_all_products="calculate_all_products"
				@global_form_didnt_validate="global_form_didnt_validate"
			/>

			<!-- Product filters -->
			<Filters
				v-if="display_filters"
				:got_products="got_products"
				:filtered_array="filtered_array"
				:global_disabled="global_disabled"
				@calculate_all_products="calculate_all_products"
			/>

		</div>

		<!-- Vrchny loading, zmizne ked sa nacitaju vsetky produkty -->
		<!-- <Bouncer_loading
			v-show="loaders_active > 0"
			style="margin: auto; display: block; position: relative; top: -28px; height: 0px;"
		/> -->

		<!-- Vypis vsetkych produktov -->
		<div v-if="valid_global_form">
			<TransitionGroup name="product_shuffle" >
				<Product_template
					v-for="product_id in only_displayed_products"
					:key="product_id"
					:product_id="product_id"
					:product_name="got_products[product_id]"
					:response="raw_responses[product_id]"
					:being_calculated="being_calculated"
					@recalculate_product="recalculate_product"
				/>
			</TransitionGroup>
		</div>

		<!-- Spodny loading, zobrazi sa ked sa nacita prvy produkt, zmizne ked sa nacitaju vsetky produkty -->
		<!-- <Bouncer_loading
			v-show="loaders_active > 1"
			style="margin: auto; display: block; position: relative; top: -16px; height: 0px;"
		/> -->

	</section>
</template>

<script>
	import Product_template from '@/components/form_app/product/Product_template.vue';
	import Product_requests from '@/logic/requests/Product_requests.js';
	import Step_title from '@/components/form_app/product/Step_title.vue';
	import Summary from '@/components/form_app/product/Summary.vue';
	import Filters from '@/components/form_app/product/Filters.vue';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import Global_form from '@/components/form_app/product/Global_form.vue';
	import Calculate_requests from '@/logic/requests/Calculate_requests.js';
	import Product_helpers from '@/components/form_app/product/Product_helpers.js';
	import STATE_params from '@/store/STATE_params.js';
	import Session_data from '@/logic/requests/Session_data.js';

	export default {
		props: {
		},
		components: {
			Product_template,
			Step_title,
			Summary,
			Filters,
			Bouncer_loading,
			Global_form,
		},
		data() {
			return {
				// loaders_active: 1, // number of loaders displayed
				loaded: 0,
				total: 0,
				got_products: {
					// 245: 'Allianz Cestovné',
				},
				product_order: [
					// 245, 251, ...
				],
				found_with_error: [
					// 245, ...
				],
				raw_responses: {
					// 245: { whole response },
				},
				being_calculated: [
					// 245, ...
				],
				udaje: '',
				period: 0,
				global_disabled: true,
				valid_global_form: true,
				ready_filters: false,
			};
		},
		created() {
			// first get product list
			Product_requests.get_product_list().then(
				(response) => {

					// store for component loop
					this.got_products = response.result.products;

					// filters can init since we got_products
					this.ready_filters = true;

					// filtered stays in state
					STATE_params().set_filtered_products(response.result.filtered_products);

					// calculate each product
					this.calculate_all_products();

				}
			);
		},
		methods: {
			calculate_all_products()
			{
				// reset state values
				this.global_disabled = true;
				this.valid_global_form = true;
				this.loaded = 0;
				let allowed = this.reduce_products_by_filtered();
				this.total = Object.keys(allowed).length;
				this.found_with_error = [];
				// this.raw_responses = {}; we used to remove response to indicate loading, now keed data just add animation

				// initial order comes from getproducts api
				this.product_order = [];
				for (let key in this.got_products) { if (!this.got_products.hasOwnProperty(key)) { continue; }
					this.product_order.push(parseInt(key, 10));
				}

				// add loading to all
				this.being_calculated = this.product_order;

				// check for sessions period before calculate, could change in global_form validate
				Session_data.get_data().then(
					(response) => {

						// period parameter speeds up calculate, either get sessions default, or get new value after global_form changed
						this.period = Product_helpers.find_period(response);

						// loop all allowed and calculate each of them
						for (let product_id in allowed) { if (!allowed.hasOwnProperty(product_id)) { continue; }
							this.calculate_product(parseInt(product_id, 10), null);
						}

					}
				);
			},
			calculate_product(product_id, form_data)
			{
				// form_data is null on initial load or global_form change, it is payload string if product_form changes
				// calculate also solves 408 with results request
				Calculate_requests.get_calculate(product_id, form_data, this.period).then(
					(response) => {

						// raw response is used inside template
						this.raw_responses[product_id] = response;

						// error
						if (
							response.result === undefined || // 500
							response.result.error !== undefined ||
							response.result[product_id] === undefined
						) {
							console.log('#' + product_id + ' had error', response);

							// hide product template
							this.found_with_error.push(product_id);

							// count loaded
							this.total--;
							this.count_loaded();
							return;
						}

						// udaje
						this.udaje = Product_helpers.search_for_udaje(response);

						// save call_params for hide-condition + input values
						STATE_params().store_call_params(response.result.call_params);

						// update order with each finished calculate
						// recalculate of one product doesnt update order
						if (form_data === null) {
							this.product_order = Product_helpers.find_order(response, this.product_order, product_id);
						}

						// count loaded
						this.loaded++;
						this.count_loaded();

						// remove loading from this one
						let index = this.being_calculated.indexOf(product_id);
						if (index > -1) {
							delete this.being_calculated[index];
						}
					}
				);
			},
			recalculate_product(product_id, form_data)
			{
				// product form changed, update counter
				this.loaded--;
				this.count_loaded();

				// purge old response
				// delete this.raw_responses[product_id]; we used to remove response to indicate loading, now keed data just add animation
				// add loading for this one
				this.being_calculated.push(product_id);

				// and run calculate
				this.calculate_product(product_id, form_data);
			},
			count_loaded()
			{
				// all finished
				if (this.loaded === this.total) {
					// this.loaders_active = 0;
					this.global_disabled = false;
				}
				// some running
				else {
					// this.loaders_active = 2;
					this.global_disabled = true;
				}
				// loaders_active sa mi buguje v pripade ze sa nacitava len jeden produkt
				// asi nacachovana property, docasne som ho vypol
			},
			reduce_products_by_filtered()
			{
				// calculate only filtered products
				// use all if filtered are missing
				if (this.filtered_array.length === 0) {
					return this.got_products;
				}

				// but keed order from got_products
				let allowed = [];
				for (let key in this.got_products) { if (!this.got_products.hasOwnProperty(key)) { continue; }
					if (this.filtered_array.indexOf(parseInt(key, 10)) === -1) {
						continue;
					}
					allowed[key] = this.got_products[key];
				}

				// filtered with good order
				return allowed;
			},
			global_form_didnt_validate()
			{
				this.valid_global_form = false;
			},
		},
		computed: {
			user_data() {
				return this.udaje;
			},
			only_displayed_products()
			{
				// component loop only for displayed products
				let displayed = [];

				// order is taken from product_order
				let i, len = this.product_order.length;
				for (i = 0; i < len; i++) {

					// id
					let id = parseInt(this.product_order[i], 10);

					// hide if had error
					// was in filtered if filtered setting exists
					if (
						this.found_with_error.indexOf(id) !== -1
						||
						(
							this.filtered_array.length !== 0
							&&
							this.filtered_array.indexOf(id) === -1
						)
					) {
						continue;
					}

					// passed
					displayed.push(id);
				}

				// product passed filters
				return displayed;
			},
			filtered_array()
			{
				// filtered is stored as string of numbers in state '217,219'
				let string = STATE_params().get_filtered_products()?.split(',');

				// transform them into array of integers
				let filtered_array = [];
				let i, len = string.length;
				for (i = 0; i < len; i++) {

					// skip when filtered is empty
					if (string[i] === '') {
						continue;
					}

					// add INT into array
					filtered_array.push(parseInt(string[i], 10));

				}

				// [217, 219]
				return filtered_array;
			},
			display_filters()
			{
				// it waits for getproducts api
				if (!this.ready_filters) {
					return false;
				}

				// uses call_params from calculate
				let receiver = STATE_params().get_call_param('receiver');

				// only najpoistenie receiver (2) only for agents
				if (
					receiver === '' ||
					receiver === '2' && STATE_params().direct_user_no_agent()
				) {
					return false;
				}

				return true;
			},
		},
	}
</script>

<style lang="css" scoped>
	/* product animation */
	.product_shuffle-move,
	.product_shuffle-enter-active,
	.product_shuffle-leave-active {
		transition: all 0.7s ease;
	}
	.product_shuffle-enter-from,
	.product_shuffle-leave-to {
		opacity: 0;
		transform: translateX(30px);
	}
	.product_shuffle-leave-active {
		position: absolute;
	}
</style>
